body {
  font-family: sans-serif;
  background-color: #f0f2f5;
  margin: 0;
  overflow: hidden;
}

.App {
  background-color: white;
  padding: 20px 20px 0 20px; /* Adjusted padding */
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

.App-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 20px;
  border-bottom: 1px solid #ddd;
  flex-shrink: 0;
}

.App-header h1 {
  margin: 0;
  color: #1c1e21;
  font-size: 22px;
}

.header-left {
  width: 50px;
}

.header-right {
  width: auto;
  display: flex;
  gap: 20px;
  justify-content: flex-end;
}

.hamburger-btn, .header-btn {
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  z-index: 1100;
}

.header-btn {
  display: block;
  font-size: 16px;
  font-weight: 600;
  color: #1877f2;
}

.hamburger-btn {
  display: none;
  flex-direction: column;
  justify-content: space-around;
  width: 30px;
  height: 24px;
}

.hamburger-btn span {
  width: 100%;
  height: 3px;
  background-color: #333;
  border-radius: 2px;
}

.App-main {
  display: flex;
  gap: 20px;
  flex-grow: 1;
  overflow: hidden;
  position: relative;
  margin-top: 20px;
}

.memo-list-container { width: 40%; border: 1px solid #ddd; border-radius: 6px; overflow-y: auto; transition: transform 0.3s ease-in-out; background-color: white; }
.memo-view-container { width: 60%; border: 1px solid #ddd; border-radius: 6px; padding: 10px; display: flex; flex-direction: column; }
.memo-view-container textarea { flex-grow: 1; border: none; resize: none; font-family: inherit; font-size: 16px; line-height: 1.5; 
  /* background-color: #f9f9f9; */
  border-radius: 4px; padding: 10px; }
.memo-view-container textarea:focus { outline: none; }
.memo-view-container p { margin: auto; color: #888; }
.memo-list { list-style: none; padding: 0; margin: 0; text-align: left; }
.memo-list li { /*display: flex;*/ justify-content: space-between; align-items: center; padding: 10px; border-bottom: 1px solid #eee; cursor: pointer; }
.memo-list li:hover { background-color: #f0f0f0; }
.memo-list li.selected { background-color: #e7f3ff; font-weight: bold; }
.memo-list li:last-child { border-bottom: none; }
.memo-list li span { flex-grow: 1; white-space: nowrap; overflow: hidden; text-overflow: ellipsis; margin-right: 10px; }
.overlay { display: none; }

@media (max-width: 768px) {
  .App { padding: 15px; }
  .App-header { padding-bottom: 15px; }
  .App-main { margin-top: 15px; }
  .hamburger-btn { display: flex; }
  .memo-list-container { position: absolute; top: 0; left: 0; height: 100%; width: 80%; max-width: 300px; transform: translateX(-100%); z-index: 1000; border-right: 1px solid #ddd; border-radius: 0; }
  .memo-list-container.visible { transform: translateX(0); box-shadow: 4px 0px 15px rgba(0,0,0,0.1); }
  .memo-view-container { width: 100%; }
  .overlay { position: absolute; top: 0; left: 0; width: 100%; height: 100%; background-color: rgba(0, 0, 0, 0.5); z-index: 900; }
  .memo-list-container.visible + .overlay { display: block; }
}